import React from "react";
import {useQuery, gql} from '@apollo/client';
import ContentLoader, { Facebook } from 'react-content-loader'
const ABOUT_QUERY = gql` {
    setting {
        about
        profileImage {
            url
        }
    }
}`
const AboutInfo = () => {
    const { loading, error, data } = useQuery(ABOUT_QUERY)
    if (loading) return <Facebook/>
    if (error) return <p>Error :( </p>;
    const {about, profileImage} = data.setting
    return (
        <div className="about_me">
                <div className="about_large_title d-none d-lg-block">
                    About
                </div>
                <div className="container">
                    <div className="row align-items-center">
                        <div className="col-xl-6 col-md-6">
                            <div className="about_e_details">
                                <h3>About me</h3>
                                <p>{about}</p>
                                <div className="download_cv">
                                    <a className="boxed-btn3" href="#">Download CV</a>
                                </div>
                            </div>
                        </div>
                        <div className="col-xl-6 col-md-6">
                            <div className="about_img">
                                <div className="color_pattern d-none d-lg-block">
                                    <img src="img/about/color_grid.png" alt=""/>
                                </div>
                                <div className="my_Pic">
                                    <img src={`https://arcane-shore-71313.herokuapp.com/${profileImage.url}`} alt="Phat's profile description"/>
                                </div>
                            </div>
                        </div>
                    </div>
                </div>
            </div>
    )
}
export default AboutInfo