import React from "react";
import {Helmet} from 'react-helmet'
import { BallBeat } from 'react-pure-loaders';
import Bradcam from "../../components/Bradcam";
import ProjectDiscuss from "../../components/ProjectDiscuss";
import CustomerCarousel from "../../components/CustomerCarousel";
import { Link } from "react-router-dom";
import ServiceList from "../../components/ServiceList";
const ServicePage = () => {
    return (
        <>
            <Helmet>
                <title>My Service</title>
                <meta name="description" content="Phat's Service Page" />
            </Helmet>
            <Bradcam title={'Services'}/>
            <ServiceList/>


            <div className="project_area">
                <div className="container">
                    <div className="row align-items-center">
                        <div className="col-xl-6">
                            <div className="projects_thumb">
                                <img src="img/about/projects.png" alt=""/>
                            </div>
                        </div>
                        <div className="col-xl-6">
                            <div className="projects_info">
                                <h3>Colorlib Project</h3>
                                <p>Lorem ipsum dolor sit amet, consectetur adipiscing elit sed do eiusmod tempor
                                    incididunt ut labore et dolore magna aliqua. Quis ipsum suspendisse ultrices gravida
                                    Risus com odo viverra maecenas.</p>
                                <Link className="boxed-btn3" to="/portfolio" >Browse Products</Link>
                            </div>
                        </div>
                    </div>
                </div>
            </div>
            <CustomerCarousel/>
            <ProjectDiscuss/>
        </>
    )
};
export default ServicePage
