import React from "react";
import {Helmet} from 'react-helmet'
import Bradcam from "../../components/Bradcam";
import ContactInfo from "../../components/ContactInfo";

const ContactPage = () => {
    return (
        <>
            <Helmet>
                <title>Contact</title>
                <meta name="description" content="Phat's Contact Page" />
            </Helmet>
            <Bradcam title={'Contact Us'}/>
            <section className="contact-section section_padding">
                <div className="container">
                    <div className="d-none d-sm-block mb-5 pb-4">
                        <div id="map" style={{height: 480}}>

                        </div>
                    </div>
                    <div className="row">
                        <div className="col-12">
                            <h2 className="contact-title">Get in Touch</h2>
                        </div>
                        <div className="col-lg-8">
                            <form className="form-contact contact_form" action="contact_process.php" method="post"
                                  id="contactForm" noValidate="novalidate">
                                <div className="row">
                                    <div className="col-12">
                                        <div className="form-group">

                                            <textarea className="form-control w-100" name="message" id="message"
                                                      cols="30" rows="9" onFocus="this.placeholder = ''"
                                                      onBlur="this.placeholder = 'Enter Message'"
                                                      placeholder='Enter Message'></textarea>
                                        </div>
                                    </div>
                                    <div className="col-sm-6">
                                        <div className="form-group">
                                            <input className="form-control" name="name" id="name" type="text"
                                                   onFocus="this.placeholder = ''"
                                                   onBlur="this.placeholder = 'Enter your name'"
                                                   placeholder='Enter your name'/>
                                        </div>
                                    </div>
                                    <div className="col-sm-6">
                                        <div className="form-group">
                                            <input className="form-control" name="email" id="email" type="email"
                                                   onFocus="this.placeholder = ''"
                                                   onBlur="this.placeholder = 'Enter email address'"
                                                   placeholder='Enter email address'/>
                                        </div>
                                    </div>
                                    <div className="col-12">
                                        <div className="form-group">
                                            <input className="form-control" name="subject" id="subject" type="text"
                                                   onFocus="this.placeholder = ''"
                                                   onBlur="this.placeholder = 'Enter Subject'"
                                                   placeholder='Enter Subject'/>
                                        </div>
                                    </div>
                                </div>
                                <div className="form-group mt-3">
                                    <button type="submit" className="button button-contactForm btn_4 boxed-btn">Send
                                        Message
                                    </button>
                                </div>
                            </form>
                        </div>
                        <div className="col-lg-4">
                            <ContactInfo/>
                        </div>
                    </div>
                </div>
            </section>
        </>
    )
};
export default ContactPage
