import React from "react";
import {Helmet} from 'react-helmet';
import {useQuery, gql} from '@apollo/client';
import { BallBeat } from 'react-pure-loaders';
import IndexSlider from "../../components/IndexSlider";
import ProjectDiscuss from "../../components/ProjectDiscuss";
import CustomerCarousel from "../../components/CustomerCarousel";
import PortfolioShow from "../../components/PortfolioShow";
import ServiceList from "../../components/ServiceList";
import AboutInfo from "../../components/AboutInfo";
import ProjectCounter from "../../components/ProjectCounter";

const IndexPage = () => {
    return (
        <>
        <Helmet>
            <title>{`Phat's Portfolio Homepage`}</title>
            <meta name="description" content={`Phat's Portfolio Homepage`} />
        </Helmet>
        <IndexSlider/>
        <ServiceList/>
            <div className="portfolio_area portfolio_bg_1">
                <div className="container">
                    <div className="row">
                        <div className="col-xl-12">
                            <div className="section_title white_text text-center">
                                <span>Portfolios</span>
                                <h3>Some of my awesome <br/>
                                    projects here</h3>
                            </div>
                        </div>
                    </div>
                </div>
            </div>
            <div className="portfolio_image_area">
            <PortfolioShow/>
            </div>
            <AboutInfo/>
            <ProjectCounter/>
            <CustomerCarousel/>
            <ProjectDiscuss/>
            </>
    )
};
export default IndexPage
